import React from 'react'
import Layout from '../../containers/Layout'
import H2 from '../../components/Headings/H2'
import H4 from '../../components/Headings/H4'
import Lead from '../../components/Text/Lead'
import P from '../../components/Text/P'
import CodeCard from '../../components/CodeCard'
import ExampleSection from '../../components/ExampleSection'

const buttonSizesCode = `
<button class="rounded-lg px-6 h-12 text-lg text-indigo-100 bg-indigo-700 focus:shadow-outline">Large</button>
<button class="rounded-lg px-5 h-10 text-indigo-100 bg-indigo-700 focus:shadow-outline">Regular</button>
<button class="rounded-lg px-4 h-8 text-sm text-indigo-100 bg-indigo-700 focus:shadow-outline">Small</button>
<input class="border rounded-lg px-4 h-12 text-gray-700 placeholder-gray-600 text-lg w-full focus:shadow-outline mb-2" type="text" placeholder="Large input">
<input class="border rounded-lg px-3 h-10 text-gray-700 placeholder-gray-600 text-base w-full focus:shadow-outline mb-2" type="text" placeholder="Regular input">
<input class="border rounded-lg px-2 h-8 text-gray-700 placeholder-gray-600 text-sm focus:shadow-outline w-full" type="text" placeholder="Small input">
`

function Buttons() {
  return (
    <Layout>
      <H2>Buttons</H2>
      <Lead>Button components using Tailwind CSS to speed up your project.</Lead>
      <hr className="mb-3" />

      <ExampleSection id="button-sizes">
        <H4>Button sizes</H4>
        <P>Different sizes of a button component.</P>
        <CodeCard lang="html" code={buttonSizesCode} />
      </ExampleSection>
    </Layout>
  )
}

export default Buttons
